import request from '../../utils/request.js'
export default {
	data() {
		return {
			cateGoryList: [],
			navIndex: 0
		};
	},
	mounted() {
		this.getCateGoryData()
	},
	methods: {
		async getCateGoryData(){
			this.cateGoryList = await request('/getCateGoryData')
		},
		// 点击导航切换的回调
		changeNavIndex(navIndex){
			this.navIndex = navIndex
		}
	}
}
